:root{font-size:62.5%}html{background:#fff}body{font:1.6rem/1.7 "游ゴシック","Yu Gothic",YuGothic,"ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic ProN","ヒラギノ角ゴ ProN",Meiryo,"メイリオ",Helvetica,Arial,Sans-Serif;font-weight:500;font-feature-settings:"pwid";background:#fff}*:hover{transition:.2s ease}ul{list-style:none}@media screen and (max-width: 1250px){:root{font-size:50%}.wrap{width:90%}}@media screen and (max-width: 768px){:root{font-size:40%}.wrap{width:95%}}header .header__logo img{width:20rem}.content__posts li{width:100%;height:30rem;margin:20rem 0;background-color:#f00}footer{text-align:center;padding:4rem 0 3rem;background-color:#223;margin:6rem 0 0;color:#fff;font-size:2rem;width:100%}#gotop{display:block;width:5rem;height:5rem;border:0.2rem solid #223;border-radius:.6rem;background-color:#fff;box-shadow:1px 1px 3px rgba(0,0,0,0.2);font-size:2.4rem;line-height:4.9rem;text-align:center;text-decoration:none;position:fixed;bottom:1rem;right:1rem;color:#223;opacity:.4}#gotop:hover,#gotop:active{opacity:.6;transition:.3s}
